@import "https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,700;1,400;1,700&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  max-width: 600px;
  width: 80vw;
  margin: 0 auto;
}

.center {
  position: relative;
  left: 50%;
  transform: translate(-50%);
}

.sticky {
  z-index: -1;
  position: sticky;
  top: 0;
}

p {
  max-width: 600px;
  margin: 0 auto;
  font-family: Playfair Display, Georgia, serif;
  line-height: 1.7;
}

svg text {
  font-family: Open Sans, Helvetica, sans-serif;
  font-size: 12px;
}

.step {
  height: 90vh;
}

.step p {
  background-color: #fffe;
  padding: 20px;
}

section.header {
  width: 90vw;
  max-width: 800px;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 200px 0 100px;
  display: flex;
}

section.header .hed, section.header .subhed {
  font-family: Playfair Display, Georgia, serif;
  font-weight: 400;
}

section.header .globe-canvas {
  width: 30vw;
  max-width: 266.667px;
  aspect-ratio: 1 / 1;
}

section.intro .intro-dots {
  aspect-ratio: 1 / 1;
  top: 30vh;
}

section.intro .intro-dots figcaption {
  font-family: Open Sans, Helvetica, sans-serif;
  font-size: 10px;
  position: absolute;
  bottom: 0;
  left: 0;
}

section.intro .intro-dots svg {
  position: absolute;
}

section.intro .intro-dots span {
  text-align: right;
  text-transform: capitalize;
  font-family: Open Sans, Helvetica, sans-serif;
  font-size: 12px;
  transition: top .2s;
  position: absolute;
  left: -10px;
  transform: translate(-100%);
}

section.education {
  min-height: 300px;
}

section.education .education-chart {
  aspect-ratio: 1 / 1;
  max-height: 80vh;
  padding-top: 10vh;
}

div.tooltip {
  max-width: 200px;
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 2px;
  padding: 7px 5px;
  font-family: Open Sans, Helvetica, sans-serif;
  font-size: 12px;
}

p.notes {
  font-family: Open Sans, Helvetica, sans-serif;
  font-size: 12px;
  font-style: italic;
}

section.occupation {
  width: 90vw;
  max-width: 800px;
  margin-top: 100px;
}

section.occupation figure {
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 40px;
  display: flex;
  position: relative;
}

section.occupation figure .wrapper {
  position: relative;
}

section.occupation figure .wrapper span {
  font-family: Open Sans, Helvetica, sans-serif;
  font-size: 12px;
  position: absolute;
  top: -2px;
  left: 15px;
}

h2 {
  width: 80vw;
  max-width: 600px;
  margin: 0 auto;
  font-family: Playfair Display, Georgia, serif;
}

h3 {
  max-width: 600px;
  width: 80vw;
  margin: 0 auto;
