@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,700;1,400;1,700&display=swap');

$sans: "Open Sans", "Helvetica", sans-serif;
$serif: "Playfair Display", Georgia, serif;


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  max-width: 600px;
  width: 80vw;
  margin: 0 auto;
}

.center {
  position: relative;
  left: 50%;
  transform: translate(-50%, 0);
}

.sticky {
  position: sticky;
  z-index: -1;
  top: 0;
}

p {
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.7;
  font-family: $serif;
}

svg {
  text {
    font-family: $sans;
    font-size: 12px;
  }
}

.step {
  height: 90vh;

  p {
    background-color: #FFFE;
    padding: 20px;
  }
}

section.header {
  width: 90vw;
  max-width: 800px;
  margin: 200px 0 100px 0;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  .hed, .subhed {
    font-family: $serif;
    font-weight: 400;
  }

  .globe-canvas {
    width: 30vw;
    max-width: calc(800px / 3);
    aspect-ratio: 1 / 1;
  }
}

section.intro {
  .intro-dots {
    top: 30vh;
    aspect-ratio: 1 / 1;

    figcaption {
      position: absolute;
      bottom: 0;
      left: 0;
      font-family: $sans;
      font-size: 10px;
    }

    svg {
      position: absolute;
    }

    span {
      text-align: right;
      text-transform: capitalize;
      transform: translate(-100%, 0);
      font-family: $sans;
      font-size: 12px;

      position: absolute;
      left: -10;
      transition: top 0.2s;
    }
  }
}

section.education {
  min-height: 300px;
  .education-chart {
    aspect-ratio: 1 / 1;
    max-height: 80vh;
    padding-top: 10vh;
  }
}

div.tooltip {
  max-width: 200px;
  font-family: $sans;
  font-size: 12px;
  background-color: white;
  padding: 7px 5px;
  border: 1px solid black;
  border-radius: 2px;
}

p.notes {
  font-style: italic;
  font-family: $sans;
  font-size: 12px;
}

section.occupation {
  margin-top: 100px;
  width: 90vw;
  max-width: 800px;
  figure {
    margin-top: 40px;
    display: flex;
    width: 100%;
    justify-content: space-around;
    flex-wrap: wrap;
    position: relative;

    .wrapper {
      position: relative;
      span {
        font-family: $sans;
        font-size: 12px;
        position: absolute;
        top: -2;
        left: 15;
      }
    }
  }
}


// FIXME: HACKS THAT WE SHOULD CHANGE
h2 {
  width: 80vw;
  max-width: 600px;
  margin: 0 auto;
  font-family: $serif;
}

h3 {
  font-family: $serif;
  font-size: 1em;
  font-weight: 400;
  font-style: italic;
  max-width: 600px;
  width: 80vw;
  margin: 0 auto;
  line-height: 1.7;

}
